import React from "react";
import {WindBarb} from './windbarbs'
import { WeatherBlob } from "./weatherBlob";

export default class WeatherLayer extends React.Component {
    render() {
        return (
            <div>
                <WindBarb parent='#weather-layer' speed={30} angle={90} x={5} z={3} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={10} angle={180} x={6} z={1} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={45} x={8} z={6} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={20} angle={0} x={1} z={4} width={.5} height={.5}/>
                <WeatherBlob parent='#weather-layer' startX={1} startY={1} startZ={1} endX={3} endY={3} endZ={3} width={5} height={5}/>
            </div>
        );
    }
}