import React from "react";

export class Borders extends React.Component {
    entity = null;
    componentDidMount() {
        var entity = document.createElement('a-entity');
        entity.setAttribute('line', "start: -5 0 -10; end: 5 0 2; color: blue");
        entity.setAttribute('line__2', "start: 5 0 2; end: 5 0 8; color: blue");
        var container = document.querySelector(this.props.parent);
        this.entity = container.appendChild(entity);
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }


    render() {
        return null;
    }
}