import React from "react";
import { Triangle } from "./triangle";

export class OrbitLayer extends React.Component {
    constructor() {
        super();
        this.positions = [{ x: 1, y: 1 }, { x: 2, y: 1 }, { x: 2, y: 2 }, { x: 3, y: 2 }, { x: 3, y: 3 }, { x: 4, y: 3 }, { x: 4, y: 4 }];
        this.state = { index: 1 };
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            var i = this.state.index+1;
            if (i > this.positions.length - 1) {
                i = 0;
            }
            //console.log(i);
            this.setState({ index: i})
        }, 1000);
    }

    componentWillUnmount() {
        window.clearInterval(this.timer);
    }

    render() {
        return (
            <Triangle parent='#threats-layer' color='green' size={.3} x={this.positions[this.state.index].x} y={this.positions[this.state.index].y} z={.1} />
        );
    }
}