import React from "react";

export class WindBarb extends React.Component {
    barb = null;

    componentDidMount() {
        var barb = document.createElement('a-image');
        switch (true){
            case(this.props.speed <= 10):
                console.log(1);
                barb.setAttribute('src', './windBarb01.png')
                break;
            case(this.props.speed <= 20):
                console.log(2);
                barb.setAttribute('src','./windBarb02.png')
                break;
            case(this.props.speed <= 30):
                barb.setAttribute('src','./windBarb03.png')
                break;
            case(this.props.speed <= 40):
                barb.setAttribute('src', './windBarb04.png')
                break;
            case(this.props.speed <= 50):
                barb.setAttribute('src','./windBarb05.png')
                break;
            case(this.props.speed > 50):
                barb.setAttribute('src', './windBarb06.png')
                break;
            default:
                barb.setAttribute('src','./windBarb01.png')
                break;
        }
        barb.setAttribute('position', { x: this.props.x ?? 0, y: this.props.y ?? 0, z: this.props.z ?? 0 });
        barb.setAttribute('rotation', { x: 90, y: -this.props.angle - 90 ?? 0, z: 0 })
        barb.setAttribute('width', this.props.width ?? 1)
        barb.setAttribute('height', this.props.height ?? 1)

        var container = document.querySelector(this.props.parent);
        this.barb = container.appendChild(barb);

    }

    componentWillUnmount() {
        this.barb.parentNode.removeChild(this.barb);
    }

    render() {
        return null;
    }
}