import React from "react";

export class Box extends React.Component {
    entity = null;

    componentDidMount() {
        var entity = document.createElement('a-box');
        entity.setAttribute('color', this.props.color ?? 'black');
        entity.setAttribute('position', { x: this.props.x ?? 0, y: this.props.z ?? .1, z: this.props.y ?? 0});
        entity.setAttribute('width', this.props.width ?? 1);
        entity.setAttribute('height', this.props.height ?? 1);
        entity.setAttribute('depth', this.props.depth ?? 0.001);
        entity.setAttribute('rotation', { x: -90, y: this.props.rotation ? 360 - this.props.rotation : 0, z: 0})
        entity.setAttribute('transparent', this.props.transparent ?? false);
        entity.setAttribute('opacity', this.props.opacity ?? .4);
        var container = document.querySelector(this.props.parent);
        this.entity = container.appendChild(entity);
        
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }

    render() {
        return null;
    }
}