import React from "react";
import {Borders} from './borders'
import {Text} from './text'

export default class BordersLayer extends React.Component {
    render() {
        return (
            <div>
                <Borders parent='#borders-layer'/>
                <Text parent='#borders-layer' color='black' x={4} y={3.5} z={0} text='Country 1' />
                <Text parent='#borders-layer' color='black' x={5.1} y={3.5} z={0} text='Country 2' />
            </div>
        );
    }
}