import React from 'react';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ZonesLayer from './zones-layer';
import ThreatsLayer from './threats-layer';
import { OrbitLayer } from './orbit-layer';
import WeatherLayer from './weather-layer';
import BordersLayer from './borders-layer';
import { Popup } from "@progress/kendo-react-popup";
import './index.css';
import { useEffect } from 'react';
//let screenLocation = {left:0, top:0};

export default function WhiteboardMenu() {
    const [areZonesActive, setAreZonesActive] = React.useState(true);
    const [areThreatsActive, setAreThreatsActive] = React.useState(true);
    const [areLabelsActive, setAreLabelsActive] = React.useState(true);
    const [isOrbitActive, setIsOrbitActive] = React.useState(true);
    const [isWeatherActive, setIsWeatherActive] = React.useState(true);
    const [isBordersActive, setIsBordersActive] = React.useState(true);
    const [screenLocation, newClickLocation] = React.useState({left:0,top:0});
    const [mounted, componentMounted] = React.useState(false);
    const [topLeft, setTopLeft] = React.useState(false);
    const [bottomLeft, setBottomLeft] = React.useState(false);
    const [topRight, setTopRight] = React.useState(false);
    const [bottomRight, setBottomRight] = React.useState(false);
    
    const topLeftRef = React.useRef(topLeft)
    const topRightRef = React.useRef(topRight)
    const bottomLeftRef = React.useRef(bottomLeft)
    const bottomRightRef = React.useRef(bottomRight)
    useEffect(()=> {
        if(!mounted){
            
            document.addEventListener('click', (e)=>{
                let bodyWidth = parseInt(document.getElementById('body').style.width) / 2
                let bodyHeight = (parseInt(document.getElementById('body').style.height) + parseInt(document.getElementById('body').style.marginTop)) / 2
                console.log(e.screenY);
                console.log(bodyHeight)
                if(e.screenX < bodyWidth){
                    if(e.screenY < bodyHeight){
                        topLeftRef.current = !topLeftRef.current
                        setTopLeft(topLeftRef.current);
                        bottomLeftRef.current = false;
                        setBottomLeft(false);
                        topRightRef.current = false;
                        setTopRight(false);
                        bottomRightRef.current = false;
                        setBottomRight(false);
                        console.log(topLeftRef.current)
                    }else{
                        bottomLeftRef.current = !bottomLeftRef.current;
                        setBottomLeft(bottomLeftRef.current);
                        topLeftRef.current = false;
                        setTopLeft(false);
                        topRightRef.current = false;
                        setTopRight(false);
                        bottomRightRef.current = false;
                        setBottomRight(false);
                    }
                }else{
                    if(e.screenY < bodyHeight){
                        topLeftRef.current = false;
                        setTopLeft(false);
                        bottomLeftRef.current = false;
                        setBottomLeft(false);
                        topRightRef.current = !topRightRef.current;
                        setTopRight(topRightRef.current);
                        bottomRightRef.current = false;
                        setBottomRight(false);
                    }else{
                        topLeftRef.current = false;
                        setTopLeft(false);
                        bottomLeftRef.current = false;
                        setBottomLeft(false);
                        topRightRef.current = false;
                        setTopRight(false);
                        bottomRightRef.current = !bottomRightRef.current;
                        setBottomRight(bottomRightRef.current);
                    }
                }
                newClickLocation({left:e.x, top: e.y});
            })
            componentMounted(true);
        }

    }, [topLeft])
    

    return (
        <div>
            <ToggleButton value="zones" aria-label="Zones" selected={areZonesActive} onChange={() => {
                setAreZonesActive(!areZonesActive);
            }} >
                Zones
            </ToggleButton>
            <ToggleButton value="threats" aria-label="Threats" selected={areThreatsActive} onChange={() => {
                setAreThreatsActive(!areThreatsActive);
            }} >
                Threats
            </ToggleButton>
            <ToggleButton value="orbit" aria-label="Orbit" selected={isOrbitActive} onChange={() => {
                setIsOrbitActive(!isOrbitActive);
            }} >
                Orbit
            </ToggleButton>
            <ToggleButton value="labels" aria-label="Labels" selected={areLabelsActive} onChange={() => {
                setAreLabelsActive(!areLabelsActive);
            }} >
                Labels
            </ToggleButton>
            <ToggleButton value="weather" aria-label="Labels" selected={isWeatherActive} onChange={() => {
                setIsWeatherActive(!isWeatherActive);
            }} >
                Weather
            </ToggleButton>
            <ToggleButton value="borders" aria-label="Labels" selected={isBordersActive} onChange={() => {
                setIsBordersActive(!isBordersActive);
            }} >
                Borders
            </ToggleButton>
            {areZonesActive ? <ZonesLayer showLabels={areLabelsActive} /> : undefined}
            {areThreatsActive ? <ThreatsLayer showLabels={areLabelsActive} /> : undefined}
            {isOrbitActive ? <OrbitLayer showLabels={areLabelsActive} /> : undefined}
            {isWeatherActive ? <WeatherLayer showLabels={areLabelsActive} /> : undefined}
            {isBordersActive ? <BordersLayer showLabels={areLabelsActive} /> : undefined}

            <Popup style={{height:300, width:300}} offset={screenLocation} show={topLeft} popupClass={"popup-content"}>
                <p>Communication Frequencies</p>
                <p>Aviation Distress : 121.500 MHz</p>
                <p>Air Traffic Control 118.000 – 121.950 MHz</p>
                <p>Ground : 121.3 MHz</p>
                           
            </Popup>
            <Popup style={{height:300, width:200}} offset={screenLocation} show={topRight} popupClass={"popup-content"}>
                <p>HOSTILE</p>
                <p>Threat Level : Midnight</p>
                <p>Range : 50NM</p>
                <p>TOP RIGHT</p>
            </Popup>
            <Popup style={{height:300, width:400}} offset={screenLocation} show={bottomLeft} popupClass={"popup-content"}>
                <p>NOTAMS</p>
                <p>AERODROME PRECISION APPROACH RADAR OUT OF SERVICE RWY 32L. 08 JUL 16:18 2021
                    UNTIL 27 AUG 23:59 2021. CREATED: 08 JUL 16:18 2021</p>
                <p>RWY 05R/23L ALL LIGHTS OUT OF SERVICE. 08 JUL 13:54 2021 UNTIL 16 JUL 23:59 2021.
                    CREATED: 08 JUL 13:54 2021</p>
            </Popup>
            <Popup style={{height:300, width:200}} offset={screenLocation} show={bottomRight} popupClass={"popup-content"}>
                <p>Weather</p>
                <p>Wind : 40 Knots NE</p>
                <p>Visibility : 5NM</p>
                <p>*Severe Thunderstorm Warning*</p>
            </Popup>
        </div>
    );
}
/**/