import React from "react";

export class WeatherBlob extends React.Component {
    constructor() {
        super();
        this.positions = [];
        this.state = { index: 1, x:0, y:0, z:0 };
        this.blob = null;
    }

    componentDidMount() {
        this.setState({x: this.props.startX, y: this.props.startY, z: this.props.startZ})
        var blob = document.createElement('a-image');
        blob.setAttribute('src', './weather.png')
        blob.setAttribute('position', { x: this.props.startX ?? 0, y: this.props.startY ?? 0, z: this.props.startZ ?? 0 });
        blob.setAttribute('rotation', { x: -90, y: 0, z: 0 })
        blob.setAttribute('width', this.props.width ?? 1)
        blob.setAttribute('height', this.props.height ?? 1)
        blob.setAttribute('material', "alphaTest: 0.5; transparent: true;")
        blob.setAttribute('opacity', ".5")

        var container = document.querySelector(this.props.parent);
        this.blob = container.appendChild(blob);

        this.xInterval = (this.props.endX - this.props.startX) / 6.0
        this.yInterval = (this.props.endY - this.props.startY) / 6.0
        this.zInterval = (this.props.endZ - this.props.startZ) / 6.0

        this.timer = setInterval(() => {
            console.log("Hello")
            var i = this.state.index+1;
            let newX = 0
            let newY = 0
            let newZ = 0
            if (i > 7) {
                i = 0;
                newX = this.props.startX
                newY = this.props.startY
                newZ = this.props.startZ
            }else{
                newX = this.state.x + this.xInterval
                newY = this.state.y + this.yInterval
                newZ = this.state.z + this.zInterval
            }
            blob.setAttribute('position', { x: newX ?? 0, y: newY ?? 0, z: newZ ?? 0 });
            this.setState({ index: i, x: newX, y: newY, z: newZ})

        }, 1000);
    }

    componentWillUnmount() {
        this.blob.parentNode.removeChild(this.blob);
        window.clearInterval(this.timer);
    }

    render() {
        return null;
    }
}