import React from "react";
import WhiteboardMenu from "./whiteboard-menu";

class App extends React.Component {

  componentDidMount(){
    // Select the node that will be observed for mutations
    let targetNode = document.getElementById('body');

    // Options for the observer (which mutations to observe)
    var config = { attributes: true, childList: true, subtree: true };

    // Callback function to execute when mutations are observed
    var observer = new MutationObserver( () => {
        var bodyMargin = document.getElementById('body').style.marginTop
        if(bodyMargin[0] === '-'){
            bodyMargin = bodyMargin.substring(1)
        }else{
            bodyMargin = '0px'
        }
        document.getElementById("react-root").style.marginTop = bodyMargin
        bodyMargin = document.getElementById('body').style.marginLeft
        if(bodyMargin[0] === '-'){
            bodyMargin = bodyMargin.substring(1)
        }else{
            bodyMargin = '0px'
        }
        document.getElementById("react-root").style.marginLeft = bodyMargin
    })


    // Start observing the target node for configured mutations
    observer.observe(targetNode, config);
  }

  componentDidUpdate(){
    var bodyMargin = this.props.number
    if(bodyMargin[0] === '-'){
        bodyMargin = bodyMargin.substring(1)
    }else{
        bodyMargin = '0px'
    }
    console.log(bodyMargin)
    document.getElementById("react-root").style.marginTop = bodyMargin
  }
  
  render() {
    return <WhiteboardMenu />;
  }
}

export default App;
