import React from "react";

export class Triangle extends React.Component {
    entity = null;

    componentDidMount() {
        var entity = document.createElement('a-triangle');
        entity.setAttribute('color', this.props.color ?? 'black');
        entity.setAttribute('transparent', this.props.transparent ?? false);
        entity.setAttribute('opacity', this.props.opacity ?? 1);

        entity.setAttribute('geometry', 'vertexA', { x: this.props.x ?? 0, y: this.props.z ?? .1, z: (this.props.y ?? 0) - (this.props.size ?? 1) });
        entity.setAttribute('geometry', 'vertexB', { x: (this.props.x ?? 0) - (this.props.size ?? 1), y: this.props.z ?? .1, z: (this.props.y ?? 0) + (this.props.size ?? 1) });
        entity.setAttribute('geometry', 'vertexC', { x: (this.props.x ?? 0) + (this.props.size ?? 1), y: this.props.z ?? .1, z: (this.props.y ?? 0) + (this.props.size ?? 1) });

        var container = document.querySelector(this.props.parent);
        this.entity = container.appendChild(entity);
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }

    componentDidUpdate(prevProps) {
        /*console.log('updated');
        console.log(this.props);
        console.log(prevProps);*/
        if (this.props.x !== prevProps.x || this.props.y !== prevProps.y || this.props.y !== prevProps.y || this.props.size !== prevProps.size) {
            //console.log('updated');
            this.entity.setAttribute('geometry', 'vertexA', { x: this.props.x ?? 0, y: this.props.z ?? .1, z: (this.props.y ?? 0) - (this.props.size ?? 1) });
            this.entity.setAttribute('geometry', 'vertexB', { x: (this.props.x ?? 0) - (this.props.size ?? 1), y: this.props.z ?? .1, z: (this.props.y ?? 0) + (this.props.size ?? 1) });
            this.entity.setAttribute('geometry', 'vertexC', { x: (this.props.x ?? 0) + (this.props.size ?? 1), y: this.props.z ?? .1, z: (this.props.y ?? 0) + (this.props.size ?? 1) });
        }
    }

    render() {
        return null;
    }
}