import React from "react";

export class Circle extends React.Component {
    entity = null;

    componentDidMount() {
        var entity = document.createElement('a-circle');
        entity.setAttribute('color', this.props.color ?? 'black');
        entity.setAttribute('position', { x: this.props.x ?? 0, y: this.props.z ?? .1, z: this.props.y ?? 0 });
        entity.setAttribute('radius', this.props.radius ?? 1);
        entity.setAttribute('rotation', { x: -90, y: this.props.rotation ?? 0, z: 0 })
        entity.setAttribute('transparent', this.props.transparent ?? false);
        entity.setAttribute('opacity', this.props.opacity ?? 1);

        var container = document.querySelector(this.props.parent);
        this.entity = container.appendChild(entity);
    }

    componentWillUnmount() {
        this.entity.parentNode.removeChild(this.entity);
    }

    render() {
        return null;
    }
}