import React from "react";
import { Box } from './box'
import {Text} from './text'

export default class ZonesLayer extends React.Component {
    render() {
        return (
            <div>
                <Box parent='#zones-layer' color='green' x={0} y={2} width={.25} height={.50} />
                <Box parent='#zones-layer' color='blue' x={10} y={1} width={.25} height={.50} rotation={30} />
                <Box parent='#zones-layer' color='blue' x={9.50} y={1} width={.25} height={.50} rotation={30} />
                <Box parent='#zones-layer' color='blue' x={9} y={1} width={.25} height={.50} rotation={30} />
                <Box parent='#zones-layer' color='yellow' x={10} y={6} width={.25} height={.50} />
                {this.props.showLabels ? <Text parent='#zones-layer' color='black' x={4.75} y={1.85} text="KIZ1" rotation={45} /> : undefined}
                <Box parent='#zones-layer' color='yellow' x={5} y={2} width={.25} height={.5} rotation={45} />
                <Box parent='#zones-layer' color='yellow' x={10} y={6} width={.25} height={.50} />
                <Box parent='#zones-layer' color='yellow' x={10} y={6.6} width={.25} height={.50} />
                <Box parent='#zones-layer' color='yellow' x={10} y={6} width={.25} height={.50} />
                <Box parent='#zones-layer' color='yellow' x={10} y={6} width={.25} height={.50} rotation={45} />
                <Box parent='#zones-layer' color='yellow' x={10} y={6} width={.25} height={.50} />
            </div>
        );
    }
}