import React from "react";
import { Triangle } from './triangle'
import { Circle } from "./circle";
import { Text } from "./text";

export class Threat extends React.Component {
    render() {
        return <div>
            {this.props.showLabel && this.props.label ? <Text parent={this.props.parent} color={this.props.labelColor ?? 'black'} x={this.props.x} y={this.props.y -.5} z={.5} text={this.props.label} /> : undefined}
            <Triangle parent={this.props.parent} color={this.props.iconColor} size={this.props.size ?? .1} x={this.props.x} y={this.props.y} z={.1}/>
            <Circle parent={this.props.parent} color={this.props.circleColor} radius={this.props.radius} x={this.props.x} y={this.props.y} z={.1} transparent={true} opacity={0.125} />
        </div>;
    }
}
